<template>
<div>
        <div class="form-group text-right">
        <el-button type="primary" round v-b-modal.newHoliday>Neu Urlaub</el-button>
    </div>
       <el-table
      :data="getHoliday"
      style="width: 100%">
      <el-table-column
        prop="atDate"
        label="Datum">
      </el-table-column>
      <el-table-column
        prop="description"
        label="Beschreibung">
      </el-table-column>
          <el-table-column
        prop="isEdit"
        label="Edit">
        <template slot-scope="scope">
            <i v-b-modal.editHoliday @click="editHolidayItems=scope.row" class="el-icon-edit rounded-circle bg-warning text-dark p-2"></i>
        </template>
      </el-table-column>
    </el-table>
    <b-modal id="editHoliday" ref="editHoliday" hide-footer title="Urlaub">
        <div class="form-group">
            <el-date-picker
            v-model="holidayDate"
           type="date"
             placeholder="Pick a day">
    </el-date-picker>
     <span class="ml-3"> {{editHolidayItems.atDate}} </span>
        </div>
     <div class="eralp-form-control">
      <div class="eralp-input-container">
        <input v-model="editHolidayItems.description"  type="text" required />
        <label> Beschreibung </label>
      </div>
    </div>
    <div class="form-group">
        <el-button type="primary" round @click="sendEditHoliday()">Speichern</el-button>
    </div>
    </b-modal>
        <b-modal id="newHoliday" ref="newHoliday" hide-footer title="Urlaub">
        <div class="form-group">
            <el-date-picker
            v-model="elementHolidayDate"
           type="date"
             placeholder="Pick a day">
    </el-date-picker>
        </div>
     <div class="eralp-form-control">
      <div class="eralp-input-container">
        <input v-model="holidayItems.description"  type="text" required />
        <label> Beschreibung </label>
      </div>
    </div>
    <div class="form-group">
        <el-button type="primary" round @click="sendNewHoliday()">Speichern</el-button>
    </div>
    </b-modal>
</div>
</template>

<script>
import { mapGetters } from 'vuex'
import auth from '../mixins/auth';
import isAdmin from '../mixins/isGod';
import { getApi } from '../api/api';
import { dateFormatterElement,dataFormatterApiFormat } from '../dateFormatter';
export default {
      mixins:[auth,isAdmin],
    computed:{
        ...mapGetters(['getHoliday'])
    },
    watch:{
        elementHolidayDate(data){
     if(data!=null){
                let newDate = dateFormatterElement(data)
            this.holidayItems.atDate = newDate
     }
        
        else{
this.holidayItems.atDate=null
        }
        }
    },
    data(){
        return{
            editHolidayItems:[],
            holidayDate:null,
            elementHolidayDate:null,
            holidayItems:{
                atDate:null,
                description:""
            },
            errPoint:[]
        }
    },
    methods:{
        sendEditHoliday(){
            if(this.holidayDate!=null){
                let newDate = dateFormatterElement(this.holidayDate)
                this.editHolidayItems.atDate = newDate
            }
            else{
                this.editHolidayItems.atDate = dataFormatterApiFormat(this.editHolidayItems.atDate)
            }
            this.$store.dispatch('sendEditHolidayItems',this.editHolidayItems).then(result=>{
                if(result=='success'){
                    this.$notify({
                     title: "Erfolg",
                 message: "Aktualisiert",
                type: "success"
               });
                    this.$refs['editHoliday'].hide()
                }
            })
        },
        sendNewHoliday(){
            this.errPoint=[]
        if(this.holidayItems.atDate==null){
            this.errPoint.push({ err: "Datum"})
        }
         if(this.holidayItems.description==""){
            this.errPoint.push({ err: "Beschreibung"})
        }


        if(this.errPoint.length==0){
            this.$store.dispatch('sendHolidayItems',this.holidayItems).then(result=>{
                this.$refs['newHoliday'].hide()
            })
        }
        else{
            for (let i = 0; i < this.errPoint.length; i++) {
               setTimeout(() => {
                     this.$notify({
                     title: "Erfolg",
                 message: this.errPoint[i].err,
                type: "success"
               }); 
               }, i*50);
                
            }
        }
}

    },
mounted(){
 if(this.getHoliday.length==0){
     getApi('holidays').then(result=>{
         if(result.body!=null){
             this.$store.commit('setHolidays',result.body)
         }
     })
 }
},
}
</script>

